.header {
  box-shadow: 0px 2px 12px 0 rgba(0, 0, 0, 0.1);
  &__toolbar {
    width: 100%;
    height: 50px;
    mix-blend-mode: undefined;
    background-color: $greyish-brown;
    display: flex;
    align-items: center;
    color: $white-six; }
  &__primary {
    padding: 20px; }
  &__mail {
    font-size: 15px;
    display: flex;
    align-items: center;
    font-size: 15px;
    letter-spacing: 0.9px;
    color: $white;
    font-weight: 300;
    i {
      margin-right: 9px; } }
  &__phone {
    display: inline-flex;
    align-items: center;
    margin-left: 50px;
    font-size: 18px;
    color: $white;
    text-decoration: none;
    i {
      margin-right: 10px; } }
  &__callback {
    width: 130px;
    height: 30px;
    text-align: center;
    text-decoration: none;
    background-image: linear-gradient(165deg, $tealish, $greenish-teal);
    border-radius: 15px;
    margin-left: 15px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    font-weight: bold;
    color: $white; }
  &__social {
    ul {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      a {
        color: rgba($white, 0.5);
        text-decoration: none;
        font-size: 15px;
        @include tr;
        &:not(:last-child) {
          margin-right: 20px; }
        &:hover {
          color: $white; } } } }
  .logo {
    width: 240px;
    img {
      width: 100%; } }
  nav {
    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      li {
        &.active {
          a {
            color: $lipstick-five;
            &:before {
              opacity: 1; } } } }
      a {
        font-size: 18px;
        color: $greyish-brown;
        position: relative;
        @include tr;
        &:before {
          content: '';
          width: 100%;
          height: 2px;
          background: $lipstick-five;
          position: absolute;
          bottom: -43px;
          opacity: 0;
          @include tr; }
        &:hover {
          color: $lipstick-five;
          &:before {
            opacity: 1; } } } } } }
