body {
  font-family: 'Ubuntu', sans-serif;
  font-size: 22px;
}

.container {
  max-width: 1170px;
  width: 100% !important;
  margin: 0 auto;
  height: 100%;
}

.row {
  height: 100%;
}

a {
  text-decoration: none;
}

.flex {
  display: flex;
}

.heading {
  display: flex;
  justify-content: center;
  margin-bottom: 70px;
}

.heading h2 {
  font-size: 30px;
  font-weight: bold;
  line-height: 1.87;
  letter-spacing: 0.8px;
  text-align: center;
  color: #272d36;
  position: relative;
  display: inline-block;
}

.heading h2:before {
  position: absolute;
  left: 0;
  bottom: 0;
  content: '';
  width: 50px;
  height: 3px;
  background: #e61852;
}

.btn {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.7;
  letter-spacing: 2px;
  color: #ffffff;
  border-radius: 35px;
  background-image: linear-gradient(165deg, #a528c9, #e31b58);
  height: 70px;
  padding: 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  text-transform: uppercase;
}

.block__img {
  position: relative;
}

.block__img__content {
  border-radius: 25px;
  overflow: hidden;
  width: 100%;
  height: 100%;
  box-shadow: 0px 4px 20px 0 rgba(0, 0, 0, 0.25);
  height: 345px;
  background: #ffffff;
}

.block__img:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: -15px;
  left: -15px;
  background-image: linear-gradient(to top, #8016a7, #e01f59);
  z-index: -1;
  border-radius: 25px;
}

.block__img img {
  width: 100%;
}

.block__img-secondary {
  margin-top: 20px !important;
}

.block__img-secondary:before {
  top: -15px;
  right: -15px;
  left: auto;
}

.breadcrumbs {
  padding: 30px 0;
}

.breadcrumbs ul {
  display: flex;
  flex-wrap: wrap;
}

.breadcrumbs li a {
  font-size: 12px;
  line-height: 1.67;
  letter-spacing: 0.3px;
  color: #838383;
}

.breadcrumbs li a i {
  position: relative;
  top: 2px;
  display: inline-block;
  padding: 0 4px;
  margin-right: 10px;
}

.breadcrumbs li:last-child a {
  color: #e61852;
  cursor: default;
}

.breadcrumbs li:last-child a i {
  display: none;
}

.header {
  box-shadow: 0px 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.header__toolbar {
  width: 100%;
  height: 50px;
  mix-blend-mode: undefined;
  background-color: #464646;
  display: flex;
  align-items: center;
  color: #e5e5e5;
}

.header__primary {
  padding: 20px;
}

.header__mail {
  font-size: 15px;
  display: flex;
  align-items: center;
  font-size: 15px;
  letter-spacing: 0.9px;
  color: #ffffff;
  font-weight: 300;
}

.header__mail i {
  margin-right: 9px;
}

.header__phone {
  display: inline-flex;
  align-items: center;
  margin-left: 50px;
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
}

.header__phone i {
  margin-right: 10px;
}

.header__callback {
  width: 130px;
  height: 30px;
  text-align: center;
  text-decoration: none;
  background-image: linear-gradient(165deg, #28c9c2, #2cb894);
  border-radius: 15px;
  margin-left: 15px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  font-weight: bold;
  color: #ffffff;
}

.header__social ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.header__social ul a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 15px;
  transition: all 0.3s;
}

.header__social ul a:not(:last-child) {
  margin-right: 20px;
}

.header__social ul a:hover {
  color: #ffffff;
}

.header .logo {
  width: 240px;
}

.header .logo img {
  width: 100%;
}

.header nav ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header nav ul li.active a {
  color: #e61852;
}

.header nav ul li.active a:before {
  opacity: 1;
}

.header nav ul a {
  font-size: 18px;
  color: #464646;
  position: relative;
  transition: all 0.3s;
}

.header nav ul a:before {
  content: '';
  width: 100%;
  height: 2px;
  background: #e61852;
  position: absolute;
  bottom: -43px;
  opacity: 0;
  transition: all 0.3s;
}

.header nav ul a:hover {
  color: #e61852;
}

.header nav ul a:hover:before {
  opacity: 1;
}

.footer {
  position: relative;
  z-index: 12;
  background: #464646;
  padding-top: 50px;
}

.footer .up {
  width: 50px;
  height: 50px;
  background-color: #e1214e;
  box-shadow: 0px 2px 8px 0 rgba(0, 0, 0, 0.15);
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -50px;
  top: 50%;
  transform: translate(0, -50%);
}

.footer .up i {
  font-size: 20px;
  color: #ffffff;
  transform: rotate(180deg);
}

.footer__nav {
  padding-bottom: 30px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}

.footer__nav h4 {
  margin-bottom: 5px;
}

.footer__nav h4 a {
  font-size: 16px;
  font-weight: 500;
  line-height: 2.5;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.85);
}

.footer__nav ul {
  margin-top: 25px;
}

.footer__nav li a {
  font-size: 14px;
  line-height: 2.29;
  letter-spacing: 0.4px;
  color: rgba(255, 255, 255, 0.75);
}

.footer__relations {
  padding: 30px 0;
}

.footer__relations .container {
  position: relative;
}

.footer__phone a {
  font-size: 20px;
  letter-spacing: 0.5px;
  color: #fefefe;
  padding-bottom: 15px;
  display: flex;
  align-items: center;
}

.footer__phone a i {
  margin-right: 10px;
  display: inline-block;
}

.footer__social ul {
  display: flex;
  justify-content: center;
}

.footer__social ul li {
  padding-right: 30px;
}

.footer__social ul li:last-child {
  padding-right: 0;
}

.footer__social ul li i {
  font-size: 22px;
  color: #ffffff;
}

.footer__social p {
  font-size: 12px;
  line-height: 1.67;
  letter-spacing: 0.3px;
  color: #e1214e;
  padding-top: 15px;
}

.footer__social p span {
  color: #ffffff;
}

.footer__logo-algoritm {
  padding-left: 25px;
}

.footer__logo-algoritm p {
  font-size: 15px;
  font-weight: 300;
  line-height: 1.33;
  letter-spacing: 0.4px;
  color: #dddddd;
}

.footer__logo-algoritm p a {
  color: #ffffff;
}

.swiper-container {
  width: 100%;
