body {
  font-family: 'Ubuntu', sans-serif;
  font-size: 22px; }
.container {
  max-width: 1170px;
  width: 100% !important;
  margin: 0 auto;
  height: 100%; }
.row {
  height: 100%; }
a {
  text-decoration: none; }
.flex {
  display: flex; }

.heading {
  @include center-x;
  margin-bottom: 70px;
  h2 {
    font-size: 30px;
    font-weight: bold;
    line-height: 1.87;
    letter-spacing: 0.8px;
    text-align: center;
    color: $dark;
    position: relative;
    display: inline-block;
    &:before {
      position: absolute;
      left: 0;
      bottom: 0;
      content: '';
      width: 50px;
      height: 3px;
      background: $lipstick-five; } } }

.btn {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.7;
  letter-spacing: 2px;
  color: $white;
  border-radius: 35px;
  background-image: linear-gradient(165deg, $barney, $lipstick-three);
  height: 70px;
  padding: 0 50px;
  @include center;
  display: inline-flex;
  text-transform: uppercase; }


.block__img {
  position: relative;
  &__content {
    border-radius: 25px;
    overflow: hidden;
    width: 100%;
    height: 100%;
    box-shadow: 0px 4px 20px 0 rgba(0, 0, 0, 0.25);
    height: 345px;
    background: $white; }
  &:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: -15px;
    left: -15px;
    background-image: linear-gradient(to top, #8016a7, #e01f59);
    z-index: -1;
    border-radius: 25px; }
  img {
    width: 100%; }
  &-secondary {
    margin-top: 20px !important;
    &:before {
      top: -15px;
      right: -15px;
      left: auto; } } }


.breadcrumbs {
  padding: 30px 0;
  ul {
    display: flex;
    flex-wrap: wrap; }
  li {
    a {
      font-size: 12px;
      line-height: 1.67;
      letter-spacing: 0.3px;
      color: $warm-grey-two;
      i {
        position: relative;
        top: 2px;
        display: inline-block;
        padding: 0 4px;
        margin-right: 10px; } }
    &:last-child {
      a {
        color: $lipstick-five;
        cursor: default;
        i {
          display: none; } } } } }
